.PortfolioProject {
  margin-bottom: 50px;
}

.PortfolioProject-header-info {
  color: #888;
  font-size: large;
}

.PortfolioProject-header {
  padding: 0px;
}

.PortfolioProject-header h1 {
  padding: 0px;
  /* margin: 0px; */
  margin-bottom: 0px;
}

.PortfolioProject-content {
  display: flex;
}

.PortfolioProject-description {
  display: inline;
  padding: 0px;
  width: 100%;
  padding-right: 10px;
}

.PortfolioProject-description p:first-of-type {
  margin: 0px;
}

.PortfolioProject-picture {
  display: inline;
  flex: 1;
}

.PortfolioProject-picture img {
  width: 250px;
  height: 250px;
  object-fit: cover;
}

.PortfolioProject-links {
  margin: 10px 0px;
  padding: 0px;
}

.PortfolioProject-links li {
  display: inline-block;
  padding: 0px;
}

.PortfolioProject-links li a {
  display: block;
  color: rgb(20, 102, 255);
  margin: 0px;
  padding-left: 0px;
  padding-right: 15px;
  text-decoration: none;
  display: flex;
  align-items: center;
}

.PortfolioProject-links li svg {
  padding-right: 5px;
}
